import { useEffect } from 'react';

import SectionHeader from '../components/UI/SectionHeader';
import ContentCard from '../components/UI/ContentCard';
import FeedBackForm from '../components/FeedBackForm';

import classes from './Contacts.module.css';
import map from '../images/map.webp';

function ContactsPage() {
    // reset scroll position
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    return (
        <main className={classes.main}>
            <SectionHeader className={classes.header}></SectionHeader>

            <section className={classes.items}>
                <ContentCard className={classes['item-card']}>
                    <h3 className={classes['item-header']}> </h3>
                    <p className={classes['item-text']}>
                         10:00  21:00 <br />  
                    </p>
                </ContentCard>

                <ContentCard className={classes['item-card']}>
                    <h3 className={classes['item-header']}></h3>
                    <p className={classes['item-text']}>
                        . , .  <br /> 67, . 112
                    </p>
                </ContentCard>

                <ContentCard className={classes['item-card']}>
                    <h3 className={classes['item-header']}></h3>
                    <p className={classes['item-text']}>+375 (29) 113-69-69</p>
                </ContentCard>

                <ContentCard className={classes['item-card']}>
                    <h3 className={classes['item-header']}>E-mail</h3>
                    <p className={classes['item-text']}>zakaz@loverflower.by</p>
                </ContentCard>
            </section>

            <div className={classes['text-us-header']}>
                <SectionHeader smallHeader={true}></SectionHeader>
                <SectionHeader smallHeader={true}></SectionHeader>
            </div>

            <FeedBackForm className={classes.feedback} contactsPage={true} />

            <div className={classes.map}>
                <h3 className={classes['item-header']}>  </h3>
                <img src={map} alt="" />
            </div>

            <div className={classes['decor-flower-top-right']}></div>

            <div className={classes['decor-ellipse-top-left']}></div>
            <div className={classes['decor-ellipse-top-right']}></div>
            <div className={classes['decor-ellipse-middle']}></div>
            <div className={classes['decor-ellipse-bottom-left']}></div>
            <div className={classes['decor-ellipse-bottom-right']}></div>
        </main>
    );
}

export default ContactsPage;
